import React from 'react';
import { Space, Popconfirm } from 'antd';
import moment from 'moment';
import intl from 'react-intl-universal';

import DataTable from 'components/DataTable';
import Button from 'components/Button';
import slugify from "@/utils/slugify";
import { check_slug } from "../../Product/service";
import messages from './../messages';

export const createColumns = (self, employees) => {
	return [
    {
      name: 'id',
      formItem: {
        type: 'hidden'
      }
    },
    {
      title: intl.formatMessage(messages.name),
      name: 'name',
      tableItem: {
        filterType: "input",
        sorter: true,
      },
      formItem: {
        onBlur: (e, form) => {
          const { getFieldValue, setFieldsValue, validateFields } = form.refs.form
          if (!getFieldValue("slug")) {
            setFieldsValue({slug: slugify(e.currentTarget.value)});
            validateFields(["slug"])
          }
        },
        rules: [
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterName)
          }
        ]
      },
    },
    {
      title: intl.formatMessage(messages.slug),
      name: 'slug',
      tableItem: {
        filterType: "input",
        sorter: true,
      },
      formItem: {
        rules: [
          ({ getFieldValue }) => ({
            validator: async (rule, value) => {
              if (!!value || value.trim() !== "") {
                const data = await check_slug({
                  id: getFieldValue("id"),
                  slug: value,
                  model: "pages"
                });
                if (data) return Promise.resolve()
                else return Promise.reject(intl.formatMessage(messages.theSlugHasAlreadyBeenTaken));
              } else return Promise.resolve()
            },
          }),
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterSlug)
          }
        ]
      }
    },
    {
      title: intl.formatMessage(messages.body),
      name: 'body',
      formItem: {
        type: 'editor',
      },
    },
    {
      title: intl.formatMessage(messages.createdAt),
      name: 'created_at',
      tableItem: {
        filterType: "datetime",
        sorter: true,
        render(text) {
          return moment(text).format("DD/MM/YYYY");
        }
      },
      searchItem: {
        type: 'datetime'
      }
    },
    {
      title: intl.formatMessage(messages.updatedAt),
      name: 'updated_at',
      tableItem: {
        filterType: "datetime",
        sorter: true,
        render(text) {
          return moment(text).format("DD/MM/YYYY");
        }
      },
      searchItem: {
        type: 'datetime'
      }
    },
    {
      title: intl.formatMessage(messages.operating),
      tableItem: {
        width: 180,
        render: (text, record) => {
          return (
            <DataTable.Oper>
              <Space>
                <Button tooltip={intl.formatMessage(messages.modify)} onClick={() => self.onUpdate(record)}>
                  <i className="las la-edit" />
                </Button>
                <Popconfirm
                  title={intl.formatMessage(messages.areYouSure)}
                  icon={<i className="las la-question-circle" />}
                  onConfirm={() => self.handleDelete([record])}
                >
                  <Button tooltip={intl.formatMessage(messages.delete)}>
                    <i className="las la-trash-alt" />
                  </Button>
                </Popconfirm>
              </Space>
            </DataTable.Oper>
          )
        }
      }
    }
  ]
};

export const createFormMenuDrag = (pages) => [
  {
    name: 'id',
    formItem: {
      type: 'hidden'
    }
  },
  {
    title: intl.formatMessage(messages.name),
    name: 'name',
    formItem: {
      rules: [
        {
          required: true,
          message: intl.formatMessage(messages.pleaseEnterName)
        }
      ]
    }
  },
  {
    title: intl.formatMessage(messages.page),
    name: 'page_slug',
    dict: pages,
    formItem: {
      type: 'select'
    },
  },
  {
    title: intl.formatMessage(messages.isHomepage),
    name: "is_homepage",
    formItem: {
      type: "switch"
    }
  },
  {
    title: intl.formatMessage(messages.status),
    name: "status",
    formItem: {
      type: "switch",
      initialValue: true,
    }
  },
]

