import React from 'react';
import { connect } from 'dva';
import { Layout, Button } from 'antd';

import BaseComponent from 'components/BaseComponent';
import Toolbar from 'components/Toolbar';
import DataTable from 'components/DataTable';
import { ModalForm, ModalDragList, } from 'components/Modal';
import { createColumns, createFormMenuDrag, createFormSlideDrag, } from './columns';
import './index.less';
import intl from "react-intl-universal";
import messages from "../messages";

const { Content, Header, Footer } = Layout;
const { Pagination } = DataTable;

@connect(({ page, loading }) => ({
  page,
  loading: loading.models.page
}))
class Page extends BaseComponent {
  constructor(props) {
    super(props);
    this.state = {
      record: null,
      visible: false,
      visibleDragMenu: false,
      visibleDragSlide: false,
      rows: []
    }
  }

  handleDelete = records => {
    const { rows } = this.state;
    const { dispatch } = this.props;

    dispatch({
      type: 'page/remove',
      payload: {
        records,
        success: () => {
          // If the operation is successful, exclude the deleted row in the selected row
          this.setState({
            rows: rows.filter(
              item => !records.some(jItem => jItem.rowKey === item.rowKey)
            )
          });
        }
      }
    });
  };

  render() {
    const { page, loading, dispatch, history, location } = this.props;
    const { pageData, menus, pages, slides } = page;
    const columns = createColumns(this);
    const { rows, record, visible, visibleDragMenu, visibleDragSlide} = this.state;

    if (Object.keys(pageData.sorts).length || Object.keys(pageData.filters).length) {
      columns.map(item => {
        if (pageData.sorts[item.name] || pageData.filters[item.name]) {
          item.tableItem = {
            ...item.tableItem,
            sortOrder: pageData.sorts[item.name],
            filteredValue: pageData.filters[item.name]
          }
        }
        return item;
      })
    }

    const dataTableProps = {
      loading,
      columns,
      rowKey: 'id',
      dataItems: pageData,
      selectType: 'checkbox',
      showNum: false,
      isScroll: true,
      selectedRowKeys: rows.map(item => item.id),
      onChange: (page) => history.replace(location.pathname +pageData.toParams(page)),
      onSelect: (keys, rows) => this.setState({ rows })
    };
    const modalFormProps = {
      loading,
      record,
      visible,
      columns,
      modalOpts: {
        width: 700
      },
      onCancel: () => {
        this.setState({
          record: null,
          visible: false
        });
      },
      // Add and modify will enter this method,
      // can use the primary key or whether there is a record to distinguish the status
      onSubmit: values => {
        values = !record ? values : {...values, id: record.id}
        dispatch({
          type: 'page/save',
          payload: {
            values,
            success: () => {
              this.setState({
                record: null,
                visible: false
              });
            }
          }
        });
      }
    };

    const dragProps = (columns, data, key, visible, visibleKey, title, maxDepth, name) => {
      return {
        loading,
        columns: columns,
        data,
        visible,
        title,
        maxDepth,
        modalOpts: {
          width: 700
        },
        onSubmit: (data, value) => {
          if (value) dispatch({ type: `page/change${name}`, payload: { value, data }});
          else dispatch({ type: "page/@change", payload: { [key]: data }})
        },
        onDrag: (data) => {
          dispatch({ type: "page/@change", payload: { [key]: data }})
        },
        onCancel: () => {
          this.setState({ [visibleKey]: false });
        },
        onSave: () => {
          dispatch({ type: `page/change${name}`, payload: { value: null, data } })
          this.setState({ [visibleKey]: false });
        },
        onDelete: (id) => {
          if (id > 0) {
            dispatch({ type: `page/remove${name}`, payload: id })
          } else {
            const deleteItem = (array) => {
              for (let i = 0; i < array.length; i++) {
                if (array[i].id === id) {
                  array.splice(i, 1);
                  i--;
                } else if (array[i].children){
                  array[i].children = deleteItem(array[i].children);
                }
              }
              return array;
            }
            dispatch({ type: "page/@change", payload: deleteItem(data) })
          }
        }
      };
    }
    const modalDrapMenuProps = dragProps(createFormMenuDrag(pages.map(item => ({ code: item.slug, codeName: item.name }))), menus, "menus", visibleDragMenu, "visibleDragMenu", intl.formatMessage(messages.menu), 1,"Menu");
    const modalDrapSlideProps = dragProps(createFormSlideDrag(), slides, "slides", visibleDragSlide, "visibleDragSlide", intl.formatMessage(messages.slide), 1,"Slide");
    return (
      <Layout className="full-layout page-page">
        <Header>
          <Toolbar
            appendLeft={
              <Button.Group>
                <Button type="primary" icon={<i className="las la-plus" />} onClick={this.onAdd}>
                  {intl.formatMessage(messages.addNew)}
                </Button>
                <Button
                  type="primary"
                  disabled={!rows.length}
                  onClick={e => this.onDelete(rows)}
                  icon={<i className="las la-trash-alt" />}
                >
                  {intl.formatMessage(messages.delete)}
                </Button>
              </Button.Group>
            }
          >
            <Button.Group>
              <Button
                type="primary"
                icon={<i className="las la-bars" />} onClick={() => this.setState({ visibleDragMenu: true })}
              >
                {intl.formatMessage(messages.menu)}
              </Button>
              <Button
                type="primary"
                icon={<i className="las la-images" />} onClick={() => this.setState({ visibleDragSlide: true })}
              >
                {intl.formatMessage(messages.slide)}
              </Button>
            </Button.Group>
          </Toolbar>
        </Header>
        <Content className="has-footer">
          <DataTable {...dataTableProps} />
        </Content>
        <Footer>
          <Pagination {...dataTableProps} />
        </Footer>
        <ModalForm {...modalFormProps} />
        <ModalDragList {...modalDrapMenuProps} />
        <ModalDragList {...modalDrapSlideProps} />
      </Layout>
    );
  }
}

export default Page;
